import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { EnumDataSource, FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType } from 'shared-code-lib';
import { Format, NotificationType, Outbound } from '../../../../blocks/InterFAX/Models/outbound';
import { WebIntegrationType } from '../../../../blocks/InterFAX/Models/webintegration';

@Component({
  selector: 'app-outbound-notification-tab',
  templateUrl: './outbound-notification-tab.component.html',
  styleUrls: ['./outbound-notification-tab.component.scss'],
})
export class OutboundNotificationTabComponent implements OnInit {
  @Input() settings: Outbound;

  get notificationNotificationType() {
    return `${this.settings.notification.notificationType}`;
  }
  set notificationNotificationType(val: string) {
    this.settings.notification.notificationType = Number(val);
  }
  get notificationFormat() {
    return `${this.settings.notification.format}`;
  }
  set notificationFormat(val: string) {
    this.settings.notification.format = Number(val);
  }
  get webIntegrationNotificationType() {
    return `${this.settings.webIntegration.notificationType}`;
  }
  set webIntegrationNotificationType(val: string) {
    this.settings.webIntegration.notificationType = Number(val);
  }
  get webIntegrationWebIntegrationType() {
    return `${this.settings.webIntegration.webIntegrationType}`;
  }
  set webIntegrationWebIntegrationType(val: string) {
    this.settings.webIntegration.webIntegrationType = Number(val);
  }

  constructor(private translateService: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
  }

  deliveryStatus: FieldDefinition;
  deliveryFormat: FieldDefinition;
  defaultReplyEmail: FieldDefinition;

  webServiceType: FieldDefinition;
  webServiceMethod: FieldDefinition;
  webServiceUrl: FieldDefinition;

  createFields = (): void => {
    this.deliveryStatus = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-outbound-fax-settings.delivery-status'),
      placeholder: '',
      required: true,
      datasource: new EnumDataSource(NotificationType, 'NotificationTypeEnum', this.translateService),
    });

    this.deliveryFormat = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-outbound-fax-settings.delivery-format'),
      placeholder: '',
      required: true,
      datasource: new EnumDataSource(Format, 'FormatEnum', this.translateService),
    });

    this.defaultReplyEmail = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-outbound-fax-settings.reply-email'),
      placeholder: this.translateService.instant('admin-outbound-fax-settings.reply-email-placeholder'),
      required: false,
    });

    this.webServiceType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-outbound-fax-settings.web-service-type'),
      placeholder: '',
      required: true,
      datasource: new EnumDataSource(NotificationType, 'NotificationTypeEnum', this.translateService),
    });

    this.webServiceMethod = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-outbound-fax-settings.web-service-method'),
      placeholder: '',
      required: true,
      datasource: this.webIntegrationTypesDataSource(),
    });

    this.webServiceUrl = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-outbound-fax-settings.web-service-url'),
      placeholder: this.translateService.instant('admin-outbound-fax-settings.web-service-url-placeholder'),
      required: true,
    });
  };

  webIntegrationTypesDataSource(): FixedDataSource {
    return new FixedDataSource([
      {
        name: this.translateService.instant('WebIntegrationTypeEnum.HTTPPost'),
        value: WebIntegrationType.HTTPPost.toString(),
      },
      {
        name: this.translateService.instant('WebIntegrationTypeEnum.XMLWebService'),
        value: WebIntegrationType.XMLWebService.toString(),
      },
    ]);
  }
}
