import { Location } from '@angular/common';
import { Component, ElementRef, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import {
  AlertsService,
  Icons,
  NumberOfAttampts,
  PageOrientation,
  PageSize,
  RenderingOptimization,
  Resolution,
  TEXTFONTSIZE,
  TEXTFONTTYPE,
  Tab,
  UserPreferencesService,
} from 'shared-code-lib';
import { Outbound, SupportedValues } from '../../../blocks/InterFAX/Models/outbound';
import { ErrorsService } from '../../../blocks/services/errors.service';
import { InterFAXService } from '../../../blocks/services/interfax.service';
import { ApiResults, Control } from '../../../blocks/services/models';
import { SendFaxService } from '../../../blocks/services/send-fax.service';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { LoadAppStateService } from '../../../blocks/services/load-app-state.service';

@Component({
  selector: 'app-admin-outbound-fax-settings',
  templateUrl: './admin-outbound-fax-settings.component.html',
  styleUrls: ['./admin-outbound-fax-settings.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AdminOutboundFaxSettingsComponent implements OnInit {
  @ViewChild('spinnerWrapper', { static: true })
  spinnerWrapper: ElementRef;

  tabs = new Array<Tab>();

  rightControls: Array<Control> = [];
  saveButton: Control;
  cancelButton: Control;

  activeTab = '';

  supportedValues: SupportedValues;
  settings: Outbound;
  originalSettings: Outbound;

  username: string;
  preferencePrefix: string;
  routeParams: Params;

  constructor(
    private translateService: TranslateService,
    private interfaxService: InterFAXService,
    private spinnerService: SpinnerStateService,
    private toastrService: ToastrService,
    private errorsService: ErrorsService,
    private route: ActivatedRoute,
    private location: Location,
    private userPreferencesService: UserPreferencesService,
    private userState: UserStateService,
    private sendFaxService: SendFaxService,
    private alertsService: AlertsService,
    private appState: LoadAppStateService
  ) {}

  ngOnInit(): void {
    this.preferencePrefix = this.sendFaxService.preferencePrefix;

    this.createButtons();

    this.createTabs();

    this.route.params.subscribe((p: Params) => {
      this.routeParams = p;
      this.username = p.username ?? this.userState.interFAXUsername;
      this.loadFaxSettings();
    });
  }

  onSave = () => {
    this.interfaxService.updateOutboundConfiguration(
      this.username,
      this.settings,
      this.onSaveSucceeded,
      this.onFailedToSave
    );
    this.spinnerService.showSavingSpinner();
  };

  onCancel = () => {
    this.returnToPreviousPage();
  };

  onSaveSucceeded = (results: ApiResults) => {
    this.spinnerService.hide();

    if (!results.success) {
      this.errorsService.showErrorsModal(results.errors, 'admin-outbound-fax-settings.save-fail');
      return;
    }

    this.reloadStateIfNeeded(this.routeParams.username);
    this.originalSettings = new Outbound(this.settings);

    if (this.routeParams.username) {
      this.toastrService.success(this.translateService.instant('admin-outbound-fax-settings.save-success'));
      this.returnToPreviousPage();
      return;
    }

    this.saveSettingsAsDefault();
    this.alertsService.success(
      this.translateService.instant('admin-outbound-fax-settings.settings-header-text'),
      this.translateService.instant('admin-outbound-fax-settings.save-success-modal'),
      false,
      {
        buttonText: this.translateService.instant('Common.Close'),
        customClass: 'success-modal large-font',
        showIcon: Icons.GreenTick,
      }
    );
  };

  reloadStateIfNeeded = (userName: string = null): void => {
    if (this.originalSettings.webIntegration.notificationType == this.settings.webIntegration.notificationType) return;
    if (this.originalSettings.webIntegration.notificationType && this.settings.webIntegration.notificationType) return;

    this.appState.reloadStateIfCurrentUserIs(userName ?? this.userState.interFAXUsername, document.documentElement);
  };

  onFailedToSave = (): void => {
    this.spinnerService.hide();
    this.toastrService.error(this.translateService.instant('admin-outbound-fax-settings.save-fail'));
  };

  saveSettingsAsDefault = () => {
    this.userPreferencesService.setPreference(`${this.preferencePrefix}${NumberOfAttampts}`, this.settings.attempts);

    this.userPreferencesService.setPreference(`${this.preferencePrefix}${PageSize}`, this.settings.defaults.pageSize);

    this.userPreferencesService.setPreference(
      `${this.preferencePrefix}${PageOrientation}`,
      this.settings.defaults.pageOrientation
    );

    this.userPreferencesService.setPreference(
      `${this.preferencePrefix}${Resolution}`,
      this.settings.defaults.pageResolution
    );

    let optimization = this.settings.defaults.pageRenderingQuality;
    if (optimization == 'Standard') optimization = 'BW';
    if (optimization == 'Fine') optimization = 'GrayScale';
    this.userPreferencesService.setPreference(`${this.preferencePrefix}${RenderingOptimization}`, optimization);

    this.userPreferencesService.setPreference(
      `${this.preferencePrefix}${TEXTFONTTYPE}`,
      this.settings.defaults.textFontType
    );
    this.userPreferencesService.setPreference(
      `${this.preferencePrefix}${TEXTFONTSIZE}`,
      this.settings.defaults.textFontSize
    );

    this.userState.userProfile.useDefaultReplyAddress = this.settings.notification.alwaysUseDefaultReplyEmail;
  };

  returnToPreviousPage = (): void => {
    this.location.back();
  };

  private loadFaxSettings = () => {
    this.spinnerService.show(this.translateService.instant('Common.LoadingData'), this.spinnerWrapper.nativeElement);

    this.interfaxService.getOutboundConfiguration(this.username, this.settingsLoaded, this.failedToLoadSettings);
  };

  settingsLoaded = (i: Outbound) => {
    this.settings = i;
    this.originalSettings = new Outbound(i);

    this.interfaxService.getOutboundSupportedValues(this.supportedValuesLoaded, this.failedToLoadSettings);
  };

  supportedValuesLoaded = (sv: SupportedValues): void => {
    this.supportedValues = sv;

    this.spinnerService.hide();
  };

  failedToLoadSettings = (err: any) => {
    this.spinnerService.hide();
  };

  activateTab = (tab: string): void => {
    this.activeTab = tab;
  };

  private createTabs = () => {
    this.tabs.push(new Tab('general', this.translateService.instant('admin-outbound-fax-settings.tab-general')));

    if (this.userState.areOutboundNotificationsAllowed()) {
      this.tabs.push(new Tab('feedback', this.translateService.instant('admin-outbound-fax-settings.tab-feedback')));
    }

    this.tabs.push(new Tab('advanced', this.translateService.instant('admin-outbound-fax-settings.tab-advanced')));
  };

  private createButtons() {
    this.saveButton = new Control(
      this.onSave,
      this.translateService.instant('admin-tenant.save'),
      'btn btn-primary btn-minwidth',
      '',
      '',
      'btn',
      false
    );

    this.cancelButton = new Control(
      this.onCancel,
      this.translateService.instant('Common.Cancel'),
      'btn btn-light btn-minwidth',
      '',
      '',
      'btn',
      false
    );

    this.rightControls.push(this.saveButton);
    this.rightControls.push(this.cancelButton);
  }
}
