import { AfterViewInit, Component, Input, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { SortDescriptor } from '@progress/kendo-data-query';
import { NgxSmartModalComponent, NgxSmartModalService } from 'ngx-smart-modal';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { ColumnDisplayType, FieldDefinition, ICombinedColumn, MetaDataType, Strings, TableItem } from 'shared-code-lib';
import { ApiResult } from '../../../../../../../../Bamba.Shared/src/models/ApiResult';
import { Outbound } from '../../../../blocks/InterFAX/Models/outbound';
import { Email } from '../../../../blocks/core/email';
import { InterFAXService } from '../../../../blocks/services/interfax.service';
import { SpinnerStateService } from '../../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../../blocks/services/user-state.service';

class Item {
  email: string;
  icon: string;
}

@Component({
  selector: 'app-outbound-smtp-tab',
  templateUrl: './outbound-smtp-tab.component.html',
  styleUrls: ['./outbound-smtp-tab.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class OutboundSmtpTabComponent implements OnInit, AfterViewInit, OnDestroy {
  @Input() settings: Outbound;

  allowedSendersItems: TableItem[] = [];
  allowedSendersColumns: ICombinedColumn[];
  allowedSendersSort: SortDescriptor;

  fieldAllowedSenderEmail: FieldDefinition;
  allowedSenderEmail: string;
  emailItem: Item;

  allowedSenderModalInstance: NgxSmartModalComponent;
  showAllowedSenderModal = false;

  fieldSendCertificateTo: FieldDefinition;
  sendCertificateTo: string;
  sendCertificateModalInstance: NgxSmartModalComponent;
  showSendCertificateModal = false;
  sendCertificateErrors = [];

  allowEmailNotifications: boolean;

  subscriptions = new Subscription();

  constructor(
    private translateService: TranslateService,
    private ngxSmartModalService: NgxSmartModalService,
    private interfaxApi: InterFAXService,
    private spinnerService: SpinnerStateService,
    private toastrService: ToastrService,
    private userStateService: UserStateService
  ) {}

  ngOnInit(): void {
    this.allowedSendersColumns = this.getColumns();

    this.createFields();

    this.allowedSendersSort = { field: '1', dir: 'asc' };

    this.allowedSendersItems = this.settings.smtp.allowedSenders.map((e) => this.createTableItem(e));

    this.allowEmailNotifications = this.userStateService.areOutboundNotificationsAllowed();
  }

  ngAfterViewInit(): void {
    this.allowedSenderModalInstance = this.ngxSmartModalService.getModal('allowedSenderModal');
    if (!this.allowedSenderModalInstance) throw new Error('Failed to initialize: allowedSenderModalInstance');

    this.subscriptions.add(
      this.allowedSenderModalInstance.onClose.subscribe(() => (this.showAllowedSenderModal = false))
    );

    this.sendCertificateModalInstance = this.ngxSmartModalService.getModal('sendCertificateModal');
    if (!this.sendCertificateModalInstance) throw new Error('Failed to initialize: sendCertificateModal');

    this.subscriptions.add(
      this.sendCertificateModalInstance.onClose.subscribe(() => (this.showSendCertificateModal = false))
    );
  }

  onAddAllowedSender = (): void => {
    this.allowedSenderEmail = '';
    this.emailItem = null;
    this.showAllowedSenderModal = true;
    this.allowedSenderModalInstance.open();
  };

  onDeleteAllowedSender = (item: Item): void => {
    this.settings.smtp.allowedSenders = this.settings.smtp.allowedSenders.filter((as) => as != item.email);
    this.allowedSendersItems = this.settings.smtp.allowedSenders.map((e) => this.createTableItem(e));
  };

  onPropertiesAllowedSender = (item: Item): void => {
    this.allowedSenderEmail = item.email;
    this.emailItem = item;
    this.showAllowedSenderModal = true;
    this.allowedSenderModalInstance.open();
  };

  onSaveAllowedSenderModal = (): void => {
    if (!this.allowedSenderEmailValid()) return;

    if (this.emailItem != null) {
      this.settings.smtp.allowedSenders = this.settings.smtp.allowedSenders.filter((as) => as != this.emailItem.email);
    }

    if (Strings.isEmpty(this.allowedSenderEmail)) return;

    if (this.settings.smtp.allowedSenders.find((x) => x.toLowerCase() === this.allowedSenderEmail.toLowerCase())) {
      this.toastrService.error(this.translateService.instant('admin-outbound-fax-settings.email-exists'));
      return;
    }

    this.spinnerService.show(
      this.translateService.instant('admin-outbound-fax-settings.validating-allowed-sender'),
      document.documentElement
    );

    this.interfaxApi.ValidateAllowedSender(
      this.allowedSenderEmail,
      this.ValidateAllowedSenderSuccess,
      this.ValidateAllowedSenderFailure
    );
  };

  ValidateAllowedSenderSuccess = (result: ApiResult) => {
    this.spinnerService.hide();

    if (!result.success) {
      this.toastrService.error(result.errors[0]);
      return;
    }
    this.settings.smtp.allowedSenders.push(this.allowedSenderEmail);
    this.allowedSendersItems = this.settings.smtp.allowedSenders.map((e) => this.createTableItem(e));
    this.onCloseAllowedSenderModal();
  };

  ValidateAllowedSenderFailure = () => {
    this.spinnerService.hide();
    this.toastrService.error(
      this.translateService.instant('admin-outbound-fax-settings.failed-to-validate-allowed-sender')
    );
  };

  onCloseAllowedSenderModal = (): void => {
    this.allowedSenderModalInstance.close();
    this.showAllowedSenderModal = false;
  };

  onSendCertificate = (): void => {
    this.showSendCertificateModal = true;
    this.sendCertificateErrors = [];

    this.sendCertificateTo = this.settings.smtp.allowedSenders.join(', ');
    if (this.settings.smtp.allowedSenders.length) this.sendCertificateTo = this.sendCertificateTo + ', ';

    this.sendCertificateModalInstance.open();
  };

  onCloseSendCertificateModal = (): void => {
    this.sendCertificateModalInstance.close();
    this.showSendCertificateModal = false;
  };

  onApplySendCertificateModal = (): void => {
    this.spinnerService.show(
      this.translateService.instant('admin-outbound-fax-settings.sending-certificate'),
      document.documentElement
    );

    this.interfaxApi.sendCertificateTo(
      this.sendCertificateTo,
      this.certificateSentSuccessfully,
      this.certificateSendFailed
    );
  };

  certificateSentSuccessfully = (): void => {
    this.spinnerService.hide();
    this.toastrService.success(
      this.translateService.instant('admin-outbound-fax-settings.sending-certificate-succeeded')
    );

    this.onCloseSendCertificateModal();
  };

  certificateSendFailed = (errors: string[]): void => {
    this.spinnerService.hide();
    this.sendCertificateErrors = errors;
  };

  createFields = (): void => {
    this.fieldAllowedSenderEmail = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-outbound-fax-settings.allowed-sender-email'),
      placeholder: this.translateService.instant('admin-outbound-fax-settings.allowed-sender-email-placeholder'),
      required: false,
    });

    this.fieldSendCertificateTo = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-outbound-fax-settings.send-certificate-to'),
      placeholder: this.translateService.instant('admin-outbound-fax-settings.send-certificate-to-placeholder'),
      required: false,
    });
  };

  createTableItem(email: string): TableItem {
    const item = new Item();
    item.email = email;
    item.icon = 'fa-envelope fa-lg';

    const tableItem = new TableItem(item, this.allowedSendersColumns, this.translateService);

    tableItem.data.defaultAction = {
      name: this.translateService.instant('admin-outbound-fax-settings.properties'),
    };

    return tableItem;
  }

  getColumns(): Array<ICombinedColumn> {
    const columns: Array<ICombinedColumn> = [];

    columns.push(this.createColumn('type', ColumnDisplayType.FontAwesome, '', 32, 'icon', false));

    columns.push(
      this.createColumn(
        'email',
        ColumnDisplayType.ActionLink,
        this.translateService.instant('admin-outbound-fax-settings.column-email'),
        200,
        'email',
        true
      )
    );

    return columns;
  }

  createColumn(
    id: string,
    displayType: number,
    columnName: string,
    width: number,
    itemProperty: string,
    stretch: boolean
  ): ICombinedColumn {
    return {
      id: 'column_' + id,
      width: width,
      stretch: stretch,
      displayType: displayType,
      headerImage: '',
      headerText: columnName,
      itemProperty: itemProperty,
      columnName: columnName,
      sortable: false,
      sortByFieldName: '',
      invertBooleanValue: false,
    };
  }

  allowedSenderEmailValid = (): boolean => {
    return this.allowedSenderEmail?.indexOf('*') === -1 && Email.isValid(this.allowedSenderEmail);
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
