import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { CoverPagesDataSource, FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType } from 'shared-code-lib';
import { IRadioButtonOption } from '../../../../../../../../Bamba.Shared/src/models/radio-buttons-group';
import { Outbound, SupportedValues } from '../../../../blocks/InterFAX/Models/outbound';
import { FontSizes } from '../../../../blocks/components/html-editor/html-editor-toolbar/editor-toolbar-constants';
import { InterFAXService } from '../../../../blocks/services/interfax.service';
import { UserStateService } from '../../../../blocks/services/user-state.service';

@Component({
  selector: 'app-outbound-fax-options',
  templateUrl: './outbound-fax-options.component.html',
  styleUrls: ['./outbound-fax-options.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class OutboundFaxOptionsComponent implements OnInit {
  @Input() settings: Outbound;
  @Input() supportedValues: SupportedValues;
  @Input() username: string;

  attempts: FieldDefinition;
  attemptIntervalMinutes: FieldDefinition;
  textCharsetEncoding: FieldDefinition;
  textFontType: FieldDefinition;
  textFontSize: FieldDefinition;
  pageSize: FieldDefinition;
  pageOrientation: FieldDefinition;
  pageResolution: FieldDefinition;
  pageRenderingQuality: FieldDefinition;
  pageScaling: FieldDefinition;
  coverPage: FieldDefinition;
  renderBackground: boolean;
  deleteAfter: boolean;
  rejectUnsupportedFiles: boolean;
  unsupportedFilesOptions: IRadioButtonOption<boolean>[] = [];
  isPCI: boolean;

  get settingsAttempts() {
    return `${this.settings.attempts}`;
  }
  set settingsAttempts(val: string) {
    this.settings.attempts = Number(val);
  }
  get settingsAttemptIntervalInMinutes() {
    return `${this.settings.attemptIntervalInMinutes}`;
  }
  set settingsAttemptIntervalInMinutes(val: string) {
    this.settings.attemptIntervalInMinutes = Number(val);
  }
  get settingsDefaultsTextFontSize() {
    return `${this.settings.defaults.textFontSize}`;
  }
  set settingsDefaultsTextFontSize(val: string) {
    this.settings.defaults.textFontSize = Number(val);
  }
  get settingsDefaultsCoverPageId() {
    return `${this.settings.defaults.coverPageId}`;
  }
  set settingsDefaultsCoverPageId(val: string) {
    this.settings.defaults.coverPageId = Number(val);
  }

  constructor(
    private translateService: TranslateService,
    private interFAXService: InterFAXService,
    private userStateService: UserStateService
  ) {}

  ngOnInit(): void {
    this.createFields();
    this.rejectUnsupportedFiles = this.settings.rejectUnsupportedFiles;
    this.unsupportedFilesOptions = [
      {
        label: this.translateService.instant(
          'admin-outbound-fax-settings.accept-messages-reject-unsupported-attachments'
        ),
        value: false,
      },
      {
        label: this.translateService.instant('admin-outbound-fax-settings.reject-unsupported-attachments'),
        value: true,
      },
    ];
  }

  addOption = (options: IFieldOptions[], option: string, translate: boolean = true): void => {
    options.push({
      name: translate ? this.translateService.instant(`SendFax-Attributes-Form.Option-Value-${option}`) : option,
      value: option,
    });
  };

  getPageSizeOptions = (): IFieldOptions[] => {
    const options = new Array<IFieldOptions>();

    this.addOption(options, 'A4', false);
    this.addOption(options, 'B4', false);
    this.addOption(options, 'Legal', false);
    this.addOption(options, 'Letter', false);

    return options;
  };

  getPageOrientationOptions = (): IFieldOptions[] => {
    const options = new Array<IFieldOptions>();

    this.addOption(options, 'Landscape');
    this.addOption(options, 'Portrait');

    return options;
  };

  getResolutionOptions = (): IFieldOptions[] => {
    const options = new Array<IFieldOptions>();

    this.addOption(options, 'Fine');
    this.addOption(options, 'Standard');

    return options;
  };

  getPageScalingOptions = (): IFieldOptions[] => {
    const options = new Array<IFieldOptions>();

    this.addOption(options, 'NoScale');
    this.addOption(options, 'FitToPage');

    return options;
  };

  getRenderingQualityOptions = (): IFieldOptions[] => {
    const options = new Array<IFieldOptions>();

    options.push({
      name: this.translateService.instant(`SendFax-Attributes-Form.Option-Value-BW`),
      value: 'Standard',
    });

    options.push({
      name: this.translateService.instant(`SendFax-Attributes-Form.Option-Value-GrayScale`),
      value: 'Fine',
    });

    return options;
  };

  getFontTypeOptions = (): IFieldOptions[] => {
    let options = this.getStringOptions(this.supportedValues.fonts);

    options.push({ name: 'Sans Serif', value: 'sans-serif' });
    options.push({ name: 'Serif', value: 'serif' });
    options.push({ name: 'Monospace', value: 'monospace' });

    options.forEach((option) => {
      option.style = "font-family:'" + option.value + "';";
      option.isInputStyled = true;
    });

    options = options.sort((a, b) => a.name.localeCompare(b.name));

    return options;
  };

  getFontSizeOptions = (): IFieldOptions[] => {
    const options: IFieldOptions[] = FontSizes.legacySizes.map((fontSize, index) => ({
      name: fontSize.key,
      value: `${index + 1}`,
      style: `font-size: ${fontSize.value};`,
    }));

    return options;
  };

  getStringOptions = (strings: string[]): IFieldOptions[] => {
    const options = new Array<IFieldOptions>();

    for (const ce of strings) {
      options.push({
        name: ce,
        value: ce,
      });
    }

    return options;
  };

  setRejectUnsupportedFiles = (value: boolean): void => {
    this.rejectUnsupportedFiles = this.settings.rejectUnsupportedFiles = value;
  };

  createFields = (): void => {
    const textCharsetEncodingDataSource = new FixedDataSource(
      this.getStringOptions(this.supportedValues.charsetEncodings)
    );
    const textFontTypeDataSource = new FixedDataSource(this.getFontTypeOptions(), true);
    const textFontSizeDataSource = new FixedDataSource(this.getFontSizeOptions());
    const pageSizeDataSource = new FixedDataSource(this.getPageSizeOptions(), true);
    const pageOrientationDataSource = new FixedDataSource(this.getPageOrientationOptions(), true);
    const pageResolutionDataSource = new FixedDataSource(this.getResolutionOptions(), true);
    const pageRenderingQualityDataSource = new FixedDataSource(this.getRenderingQualityOptions(), true);
    const pageScalingDataSource = new FixedDataSource(this.getPageScalingOptions(), true);
    const coverPageDataSource = new CoverPagesDataSource(this.interFAXService, this.translateService, this.username);

    this.attempts = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('admin-outbound-fax-options.attempts'),
      placeholder: this.translateService.instant('admin-outbound-fax-options.attempts-placeholder'),
      required: true,
      allowNegativeNumbers: false,
    });

    this.attemptIntervalMinutes = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('admin-outbound-fax-options.attemptIntervalMinutes'),
      placeholder: this.translateService.instant('admin-outbound-fax-options.attemptIntervalMinutes-placeholder'),
      required: true,
      allowNegativeNumbers: false,
    });

    this.textCharsetEncoding = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-outbound-fax-options.textCharsetEncoding'),
      placeholder: '',
      required: true,
      datasource: textCharsetEncodingDataSource,
    });

    this.textFontType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-outbound-fax-options.textFontType'),
      placeholder: '',
      required: true,
      datasource: textFontTypeDataSource,
    });

    this.textFontSize = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-outbound-fax-options.textFontSize'),
      placeholder: '',
      required: true,
      datasource: textFontSizeDataSource,
    });

    this.pageSize = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-outbound-fax-options.pageSize'),
      placeholder: '',
      required: true,
      datasource: pageSizeDataSource,
    });

    this.pageOrientation = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-outbound-fax-options.pageOrientation'),
      placeholder: '',
      required: true,
      datasource: pageOrientationDataSource,
    });

    this.pageResolution = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-outbound-fax-options.pageResolution'),
      placeholder: '',
      required: true,
      datasource: pageResolutionDataSource,
    });

    this.pageRenderingQuality = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-outbound-fax-options.pageRenderingQuality'),
      placeholder: '',
      required: true,
      datasource: pageRenderingQualityDataSource,
    });

    this.pageScaling = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-outbound-fax-options.pageScaling'),
      placeholder: '',
      required: true,
      datasource: pageScalingDataSource,
    });

    this.coverPage = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-outbound-fax-options.cover-page'),
      placeholder: this.translateService.instant('admin-outbound-fax-options.cover-page'),
      datasource: coverPageDataSource,
      defaultValue: '0',
    });
  };
}
