import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { FieldDefinition, FixedDataSource, Languages, MetaDataType } from 'shared-code-lib';

import { Countries, Outbound, SupportedValues } from '../../../../blocks/InterFAX/Models/outbound';

@Component({
  selector: 'app-outbound-general-tab',
  templateUrl: './outbound-general-tab.component.html',
  styleUrls: ['./outbound-general-tab.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class OutboundGeneralTabComponent implements OnInit {
  @Input() settings: Outbound;
  @Input() supportedValues: SupportedValues;

  faxHeader: FieldDefinition;
  defaultDialCountry: FieldDefinition;
  defaultDialAreaCode: FieldDefinition;

  faxheader: string;

  mainItems = ['{to} ', '{from} ', '{totalpages} ', '{pagenumber} '];
  dateFormats = ['{datedd/mm/yy} ', '{datedd/mm/yyyy} ', '{datemm/dd/yyyy} ', '{dateyyyy/mm/dd} ', '{datelong} '];
  timeFormats = ['{timehhmmss} ', '{timehhmm} ', '{timehhmmsstt} '];

  direction: string;

  constructor(
    private translateService: TranslateService,
    private ngxSmartModalService: NgxSmartModalService
  ) {}

  ngOnInit(): void {
    this.createFields();
    this.direction = Languages.getDirection();
  }

  onTextdrop = (p: any): void => {};

  private createFields = () => {
    const options = Countries.getAsFieldOptions(this.supportedValues.countries, this.translateService);
    const countryDataSource = new FixedDataSource(options);
    this.faxHeader = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-outbound-fax-settings.fax-header'),
      placeholder: this.translateService.instant('admin-outbound-fax-settings.fax-header-placeholder'),
      required: false,
    });

    this.defaultDialCountry = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-outbound-fax-settings.default-dial-country'),
      placeholder: this.translateService.instant('admin-outbound-fax-settings.default-dial-country-placeholder'),
      required: false,
      datasource: countryDataSource,
    });

    this.defaultDialAreaCode = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-outbound-fax-settings.default-dial-areacode'),
      placeholder: this.translateService.instant('admin-outbound-fax-settings.default-dial-areacode-placeholder'),
      required: false,
    });
  };

  addFaxHeader(): void {
    this.faxheader = this.settings.faxHeader;

    this.ngxSmartModalService.getModal('addFaxHeaderModal').open();
  }

  onDragStart(event: DragEvent, data: string): void {
    event.dataTransfer.setData('text/plain', data);
  }

  onDragEnd(): void {
    if (window.getSelection) {
      if (window.getSelection().empty) {
        // Chrome
        window.getSelection().empty();
      } else if (window.getSelection().removeAllRanges) {
        // Firefox
        window.getSelection().removeAllRanges();
      }
    } else if ((document as any).selection) {
      // IE?
      (document as any).selection.empty();
    }
  }

  onClick(event: Event): void {
    event.preventDefault();
  }

  onCancel = (): void => {
    this.closeModal();
  };

  onSaveFaxHeader(): void {
    this.settings.faxHeader = this.faxheader;
    this.faxheader = '';

    this.closeModal();
  }

  closeModal(): void {
    this.ngxSmartModalService.getModal('addFaxHeaderModal').close();
  }
}
